// @flow

import {Component, emit, h, prop} from 'photon-core';

import {PhAvatar} from './../ph-avatar/ph-avatar';

import css from './ph-user-avatar.css';

const priv = Symbol();

//todo: extends dosn't work, It only creates props defined in the Super class!
//export class PhUserAvatar extends PhAvatar {
export class PhUserAvatar extends Component {

    static sizes = {
        'small': 15,
        'medium': 25,
        'large': 35
    };

    static get props() {
        //todo: performance issue get props gets called more then ones per instance
        //console.log('PhUserAvatar.props');
        return Object.assign({}, super.props, {
            imageUrl: prop.string({
                attribute: 'image-url'
            }),
            initials: prop.string({
                attribute: true
            }),
            size: prop.string({
                attribute: true,
                default: 'medium'
            }),
            isOrganization: prop.string({
                attribute: true,
                default: 'false'
            })
        });
    }

    constructor(self:PhUserAvatar) {
        self = super(self);
        // console.log(self.tagName, 'constructor');
        bindPrivateMethods(self);
        return self;
    }

    getContent() {
        if(this.isOrganization == 'true') {
            let size = this.constructor.sizes[this.size];
            let styles = {
                fontSize: '0'
            };
            return <ph-icon name='building' size={size} style={styles}></ph-icon>
        } 
        else if(this.imageUrl) {
            return <ph-image src={this.imageUrl}
                             onerror={this[priv].onImageError}
                             onload={this[priv].onImageLoad}
                             sizing="cover"></ph-image>
        }
        else if(this.initials) {
            return <span>{this.initials}</span>
        }
    }

    renderCallback():any {
        //console.log(this.tagName, 'renderCallback', this.imageUrl);
        return (
            <div id="container">
                <style>{css.toString()}</style>
                {this.getContent()}
            </div>
        );
    }
}

function bindPrivateMethods(elem:any) {
    elem[priv] = {};
    elem[priv].onImageLoad = onImageLoad.bind(elem);
    elem[priv].onImageError = onImageError.bind(elem);
}

function onImageLoad() {
    //console.log('onload');
}

function onImageError() {
    console.log('onerror');
    this.imageUrl = null;
}

customElements.define('ph-user-avatar', PhUserAvatar);





