// @flow

import {Component, emit, h, prop} from 'photon-core';

import css from './ph-checkbox.css';

export class PhCheckbox extends Component {

    static get props() {
        return {
            label: prop.string({
                attribute: true,
                default: 'check me'
            }),
            checked: prop.boolean({
                attribute: true
            }),
            disabled: prop.boolean({
                attribute: true
            }),
            reverse: prop.boolean({
                attribute: true
            })
        }
    }

    constructor(self:PhCheckbox) {
        self = super(self);
        //console.log(self.tagName, 'constructor');
        bindPrivateMethods(self);
        return self;
    }

    connectedCallback() {
        super.connectedCallback();
        this.addEventListener('click', this[priv].handleClickCapture, true);
    }

    disconnectedCallback() {
        this.removeEventListener('click', this[priv].handleClickCapture, true);
        super.disconnectedCallback();
    }

    updatedCallback(prevProps:any):boolean {
        //console.log(this.tagName, 'updatedCallback', prevProps);
        if (!prevProps || prevProps.disabled !== this.disabled) {
            if (this.disabled) {
                this.setAttribute('aria-disabled', 'true');
                this[priv].prevTabIndex = this.tabIndex;
                this.removeAttribute('tabindex');
            }
            else {
                this.setAttribute('aria-disabled', 'false');
                this.tabIndex = this[priv].prevTabIndex;
            }
        }

        if (!prevProps || prevProps.checked !== this.checked) {
            if (this.checked) {
                this.setAttribute('aria-checked', 'true');
            }
            else {
                this.setAttribute('aria-checked', 'false');
            }
        }

        return super.updatedCallback(prevProps);
    }

    renderCallback():any {
        return (
            <span id="container" onclick={this[priv].handleClick}>
                <style>{css.toString()}</style>
                <span id="box">
                    <svg id="check" width="16" height="16" viewBox="0 0 16 16">
                        <path d="M16,5.72a0.84,0.84,0,0,1-.25.62L8,14.12a0.87,0.87,0,0,1-1.23,0L2.24,9.62a0.87,0.87,0,0,1,0-1.23L3.47,7.15a0.87,0.87,0,0,1,1.23,0L7.36,9.82,13.3,3.88a0.87,0.87,0,0,1,1.23,0l1.23,1.23A0.84,0.84,0,0,1,16,5.72Z"/>
                    </svg>
                </span>
                <span id="label">{this.label}</span>
            </span>
        );
    }
}

const priv = Symbol();

function bindPrivateMethods(elem:any) {
    elem[priv] = {};
    elem[priv].tabIndex = undefined;
    elem[priv].handleClick = handleClick.bind(elem);
    elem[priv].handleClickCapture = handleClickCapture.bind(elem);
}

function handleClick(e:Event) {
    //console.log('handleClick', e, e.defaultPrevented);
    if (this.disabled) {
        e.stopPropagation();
        return;
    }
    this.checked = !this.checked;
}

function handleClickCapture(e:Event) {
    // console.log('handleClickResult', e, e.defaultPrevented);
    if (!e.defaultPrevented) {
        window.setTimeout(() => {
            this.dispatchEvent(new Event('change'));
        });
    }
}

customElements.define('ph-checkbox', PhCheckbox);



