// @flow

import {Component, emit, h, prop} from 'photon-core';

import css from './ph-panel.css';
export class PhPanel extends Component {

    renderCallback():any {
        return (
            <div id="container">
                <style>{css.toString()}</style>
                <div id="content">
                    <slot name="content"></slot>
                </div>
            </div>
        );
    }
}

customElements.define('ph-panel', PhPanel);



