// @flow
import {Component, h, prop} from 'photon-core';

import css from './ph-avatar.css';

export class PhAvatar extends Component {

    static sizes = {
        'small': 15,
        'medium': 25,
        'large': 35
    };

    static get props() {
        //console.log('PhAvatar.props');
        return {
            image: prop.string({
                attribute: true
            }),
            initials: prop.string({
                attribute: true
            }),
            size: prop.string({
                attribute: true,
                default: 'medium'
            }),
            icon: prop.string({
                attribute: true
            })
        }
    }

    // constructor(self:PhAvatar) {
    //     self = super(self);
    //     console.log(self.tagName, 'constructor');
    //     return self;
    // }

    getContent() {
        if(this.image) {
            return <ph-image src={this.image} sizing="cover"></ph-image>
        }
        else if(this.icon) {
            let size = this.constructor.sizes[this.size];
            let styles = {
                fontSize: '0'
            };
            return <ph-icon name={this.icon} size={size} style={styles}></ph-icon>
        }
        else if(this.initials) {
            return <span>{this.initials}</span>
        }
    }

    renderCallback():any {
        return (
            <div id="container">
                <style>{css.toString()}</style>
                {this.getContent()}
            </div>
        );
    }
}

customElements.define('ph-avatar', PhAvatar);




