// @flow

import {Component, emit, h, prop} from 'photon-core';

import css from './ph-image.css';

export class PhImage extends Component {

    static get props() {
        return {
            /** @private */
            forceRender: prop.boolean(),

            disabled: prop.boolean({
                attribute: true
            }),
            src: prop.string({
                attribute: true
            }),
            alt: prop.string({
                attribute: true
            }),
            sizing: prop.string({
                attribute: true
            }),
            position: prop.string({
                attribute: true
            }),
            preload: prop.boolean({
                attribute: true
            }),
            preventLoad: prop.boolean({
                attribute: true
            }),
            fade: prop.boolean({
                attribute: true
            }),
            placeholder: prop.string({
                attribute: true
            }),
            width: prop.number({
                attribute: true
            }),
            height: prop.number({
                attribute: true
            })
        }
    }

    updatedCallback(prevProps:any):boolean {
        let prevSrc = prevProps ? prevProps.src : undefined;
        if (prevSrc !== this.src) {
            this.reset();
            this.loading = !!this.src;
        }
        return super.updatedCallback(prevProps);
    }

    reset() {
        this.loading = false;
        this.loaded = false;
        this.error = false;
    }

    getSrc() {
        return this.preventLoad ? undefined : this.src;
    }

    renderCallback():any {
        //console.log(this.tagName, 'renderCallback');
        return (
            <div>
                <style>{css.toString()}</style>
                <div id="sizedImgDiv"
                     style={
                         {  backgroundImage: this.getSrc() ? 'url("' + this.src + '")' : undefined,
                            backgroundSize: this.sizing,
                            backgroundPosition: this.sizing ? this.position : undefined,
                            backgroundRepeat: this.sizing ? 'no-repeat' : undefined
                         }
                     }
                     role="img"
                     hidden={this.computeImgDivHidden()}
                     aria-hidden={this.computeImgDivARIAHidden()}
                     aria-label={this.computeImgDivARIALabel()}>
                </div>
                <img id="img"
                     src={this.getSrc()}
                     alt={this.alt}
                     hidden={this.computeImgHidden()}
                     onload={this.onImgLoad.bind(this)}
                     onerror={this.onImgError.bind(this)}
                />
                <div id="placeholder"
                     hidden={this.computePlaceholderHidden()}
                     class={this.computePlaceholderClassName()}
                     style={{
                        backgroundImage: this.hasPlaceholder() ? 'url("' + this.getPlaceholder() + '")' : undefined,
                        backgroundSize: this.sizing,
                        backgroundPosition: this.sizing ? this.position : undefined,
                        backgroundRepeat: this.sizing ? 'no-repeat' : undefined
                     }}
                >
                </div>
            </div>
        );
    }

    onImgLoad = (e:Event) => {
        //console.log('imgOnLoad', e);
        this.dispatchEvent(new Event('load'));
        //if (e.imgTag.src !== this.src) return;
        this.reset();
        // this.loading = false;
        this.loaded = true;
        // this.error = false;
        //this.mustRender();
        this.forceRender = !this.forceRender;
    };

    onImgError = (e:Event) => {
        console.log('imgOnError', e);
        this.dispatchEvent(new Event('error'));
        // if (imgTag.src !== this.src) return;
        this.reset();
        //this.loaded = false;
        this.error = true;
        //this.mustRender();
        this.forceRender = !this.forceRender;
    };

    computePlaceholderHidden() {
        if (!this.preload || (!this.fade && !this.loading && this.loaded)) {
            return true;
        }
        else {
            return undefined;
        }
    }

    computePlaceholderClassName() {
        return (this.preload && this.fade && !this.loading && this.loaded) ? 'faded-out' : '';
    }

    computeImgDivHidden() {
        return !this.sizing;
    }

    computeImgDivARIAHidden() {
        return this.alt === '' ? 'true' : undefined;
    };

    computeImgDivARIALabel() {
        if (this.alt !== null) {
            return this.alt;
        }

        if (this.src === '') {
            return '';
        }
        //return something meaningful here.
    };

    computeImgHidden() {
        return !!this.sizing;
    }

    hasPlaceholder():boolean {
        return this.placeholder != null;
    }

    getPlaceholder():string {
        if (this.placeholder) {
            return this.placeholder;
        }
        return "data:image/gif;base64,";
    }

}

customElements.define('ph-image', PhImage);



