// @flow
import {Component, emit, h, prop} from 'photon-core';

import css from './ph-vehicle-image.css';

export class PhVehicleImage extends Component {

    static get props() {
        return {
            src: prop.string({
                attribute: true,
                default: 'http://placekitten.com/80/60'
            }),
            color: prop.string({
                attribute: true,
                default: '#4AB471'
            }),
            topLabel: prop.string({
                attribute: true,
                default: 'USED'
            }),
            bottomLabel: prop.string({
                attribute: true,
                default: 'In Stock'
            })
        }
    }

    renderCallback():any {
        let size = {
            width: 80,
            height: 60
        };
        let bottomStyle = {
            backgroundColor: this.color
        };
        return (
            <div id="container">
                <style>{css.toString()}</style>
                <div id="top">{this.topLabel}</div>
                <div id="image"><img src={this.src} {...size}/></div>
                <div id="bottom" style={bottomStyle}>{this.bottomLabel}</div>
            </div>
        );
    }
}

customElements.define('ph-vehicle-image', PhVehicleImage);




