// @flow

import {Component, emit, h, prop} from 'photon-core';

import * as iconset from './icon-assets.js'

import css from './ph-icon.css';

export class PhIconSvgImg extends Component {

    //todo: opt out of ShadowDom?
    //     this.useShadowDom = false;

    static get props() {
        return {
            icon: prop.string({
                attribute: true
            }),
            src: prop.string({
                attribute: true
            }),
            size: prop.number({
                attribute: true,
                default: 24
            }),
        }
    }

    _svgRef = (ref:any) => {this._svg = ref};

    _getContent() {
        if (this.icon) {
            return (
                <svg
                    ref={this._svgRef}
                    width={this.size}
                    height={this.size}
                    xmlns="http://www.w3.org/2000/svg"
                    xmlnsXlink="http://www.w3.org/1999/xlink"
                    viewBox="0 0 24 24"
                    preserveAspectRatio="xMidYMid meet"
                >
                </svg>
            );
        }
        else if (this.src) {
            return (
                <img src={this.src}
                     width={this.size}
                     height={this.size}></img>
            );
        }
    }

    renderCallback():any {
        let style = {width: this.size + 'px', height: this.size + 'px'};
        this.style = style;
        return (
            <div style={style}>
                <style>{css.toString()}</style>
                {this._getContent()}
            </div>
        );
    }

    renderedCallback():any {
        if (this.icon) {
            let svgContent:string = iconset[this.icon];
            let svg = this._svg;
            if (svg.__innerHTML !== svgContent) {
                svg.__innerHTML = svgContent;
                svg.innerHTML = svgContent;
            }
        }
    }

}

customElements.define('ph-icon-svg-img', PhIconSvgImg);




