// @flow

import {Component, emit, h, prop} from 'photon-core';

import css from './ph-icon-badge.css';

const sizes = {
    'small': 10,
    'medium': 14,
    'large': 18
};

export class PhIconBadge extends Component {

    static get props() {
        return {
            bordered: prop.boolean({
                attribute: true,
            }),
            icon: prop.string({
                attribute: true
            }),
            status: prop.string({
                attribute: true,
                default: 'primary'
            }),
            size: prop.string({
                attribute: true,
                default: 'large'
            })
        }
    }

    renderCallback():any {
        let size = sizes[this.size];
        let styles = {
            fontSize: '0',
            lineHeight: '0'
        };
        return (
            <div id="container">
                <style>{css.toString()}</style>
                <ph-icon name={this.icon} size={size} style={styles}></ph-icon>
            </div>
        );
    }
}

customElements.define('ph-icon-badge', PhIconBadge);



