// @flow

import {Component, emit, h, prop} from 'photon-core';

import css from './ph-test.css';

export class PhTest extends Component {

    static get props() {
        return {
            bool: prop.boolean(),
            boolA: prop.boolean({attribute: true}),
            boolNA: prop.boolean({attribute: true, default: null}),

            num: prop.number(),
            numA: prop.number({attribute: true}),
            numNA: prop.number({attribute: true, default: null}),

            str: prop.string(),
            strA: prop.string({attribute: true}),
            strNA: prop.string({attribute: true, default: null})
        }
    }

    renderCallback():any {
        let formatVal:any = this.formatVal;
        return (
            <div>
                <style>{css.toString()}</style>
                <div>{'bool = ' + formatVal(this.bool)}</div>
                <div>{'boolA = ' + formatVal(this.boolA)}</div>
                <div>{'boolNA = ' + formatVal(this.boolNA)}</div>

                <div>{'num = ' + formatVal(this.num)}</div>
                <div>{'numA = ' + formatVal(this.numA)}</div>
                <div>{'numNA = ' + formatVal(this.numNA)}</div>

                <div>{'str = ' + formatVal(this.str)}</div>
                <div>{'strA = ' + formatVal(this.strA)}</div>
                <div>{'strNA = ' + formatVal(this.strNA)}</div>
            </div>
        );
    }

    formatVal(val:any) {
        switch (typeof val) {
            case "string":
                return "'" + val + "'";
            case "number":
            case "boolean":
                return val.toString();
            default:
                if (val === null) {
                    return 'null';
                }
                return typeof val;
        }
    }

}

customElements.define('ph-test', PhTest);




