// @flow

import {Component, h, prop} from 'photon-core';

import css from './ph-banner.css';

export class PhBanner extends Component {

    static get props() {
        return {
            text: prop.string({
                attribute: true,
                default: 'Top Choice'
            }),
            active: prop.boolean({
                attribute: true,
            })
        }
    }

    renderCallback():any {
        return (
            <span id="text">
                <style>{css.toString()}</style>
                {this.text}
            </span>
        );
    }
}

customElements.define('ph-banner', PhBanner);





