// @flow

import {Component, emit, h, prop} from 'photon-core';

import css from './ph-more.css';

export class PhMore extends Component {

    static get props() {
        return {
            moreLabel: prop.string({
                attribute: true,
                default: 'Show More'
            }),
            lessLabel: prop.string({
                attribute: true,
                default: 'Show Less'
            }),
            show: prop.boolean({
                attribute: true
            }),
        }
    }

    onClick(e:Event) {
        this.show = !this.show;
    }

    renderMore() {
        return (this.show)
            ? (<div id="more">
                   <slot name="content"></slot>
               </div>)
            : null;
    }

    renderLabel() {
        var icon = (this.show) ? 'up' : 'down';
        var label = (this.show) ? this.lessLabel : this.moreLabel;
        return (<div id="label-wrapper" onclick={this.onClick.bind(this)}>
                   <ph-icon name={icon} size="10" class="primary"></ph-icon>
                   <span id="label">{label}</span>
                   <ph-icon name={icon} size="10"></ph-icon>
               </div>);
    }

    renderCallback():any {
        return (
            <div id="container">
                <style>{css.toString()}</style>
                {this.renderMore()}
                {this.renderLabel()}
            </div>
        );
    }
}

customElements.define('ph-more', PhMore);



