// @flow

import {Component, emit, h, prop} from 'photon-core';

import css from './ph-card.css';

export class PhCard extends Component {

    static get props() {
        return {
            status: prop.string({
                attribute: true
            }),
            highlight: prop.boolean({
                attribute: true
            })
        }
    }

    getSlot(name:string) {
        for(var i = 0; i < this.children.length; i++) {
            if(this.children[i].attributes.getNamedItem('slot').value === name) {
                return this.children[i];
            }
        }
        return false;
    }

    renderBanner() {
        return (this.getSlot('banner'))
            ? (<div id="banner">
                   <div id="banner-text">
                       <slot name="banner"></slot>
                   </div>
               </div>)
            : null;
    }

    renderMedia() {
        return (this.getSlot('media'))
            ? (<div id="media">
                   <slot name="media"></slot>
               </div>)
            : null;
    }

    renderFooter() {
        return (this.getSlot('footer'))
            ? (<div id="footer">
                   <slot name="footer"></slot>
               </div>)
            : null;
    }

    renderCallback():any {
        return (
            <div id="container">
                <style>{css.toString()}</style>
                {this.renderBanner()}
                <div id="inner">
                    {this.renderMedia()}
                    <div id="content">
                        <div id="header">
                            <div id="header-primary">
                                <div id="title">
                                    <slot name="title"></slot>
                                </div>
                                <div id="subtitle">
                                    <slot name="subtitle"></slot>
                                </div>
                            </div>
                            <div id="header-secondary">
                                <slot name="secondary"></slot>
                            </div>
                        </div>
                        <div id="body">
                            <slot name="body"></slot>
                        </div>
                    </div>
                </div>
                {this.renderFooter()}
            </div>
        );
    }
}

customElements.define('ph-card', PhCard);





