// @flow

import {Component, emit, h, prop} from 'photon-core';

import css from './ph-test-tester.css';

const propValues = [undefined, null, NaN, '', '0', '1', 'false', 'true', 0, 1, false, true];
const attrValues = [undefined, null, 'NaN', '', '0', '1', 'false', 'true'];

export class PhTestTester extends Component {

    static get props() {
        return {
            /** @private */
            forceRender: prop.boolean(),
        }
    }

    bool:?boolean = null;
    boolA:?boolean = false;
    boolNA:any = null;

    num:?number = null;
    numA:?number = -9;
    numNA:?number = null;

    str:?string = null;
    strA:?string = 'Hi';
    strNA:?string = null;


    phTestRef = (ref:any) => {this.phTest = ref};

    renderCallback():any {

        return (
            <div>
                <style>{css.toString()}</style>

                <br/>
                <ph-test name="phTest" ref={this.phTestRef}
                         bool={this.bool}
                         bool-a={this.boolA}
                         bool-n-a={this.boolNA}

                         num={this.num}
                         num-a={this.numA}
                         num-n-a={this.numNA}

                         str={this.str}
                         str-a={this.strA}
                         str-n-a={this.strNA}
                ></ph-test>

                <br/>
                {this.renderTestProp(this, 'bool')}
                {this.renderTestProp(this, 'boolA')}
                {this.renderTestAttr(this, 'bool-a')}
                {this.renderTestProp(this, 'boolNA')}
                {this.renderTestAttr(this, 'bool-n-a')}

                <br/>
                {this.renderTestProp(this, 'num')}
                {this.renderTestProp(this, 'numA')}
                {this.renderTestAttr(this, 'num-a')}
                {this.renderTestProp(this, 'numNA')}
                {this.renderTestAttr(this, 'num-n-a')}

                <br/>
                {this.renderTestProp(this, 'str')}
                {this.renderTestProp(this, 'strA')}
                {this.renderTestAttr(this, 'str-a')}
                {this.renderTestProp(this, 'strNA')}
                {this.renderTestAttr(this, 'str-n-a')}

            </div>
        );
    }

    renderTestProp(self:any, propName:string) {
        return (
            <div>
                {propName + ' '}
                {propValues.map((val:any) => {
                    return <button onclick={self.setPropVal.bind(self, propName, val)}>{self.formatVal(val)}</button>;
                })}
                {' ' + self.printProp(propName)}
            </div>
        )
    }

    renderTestAttr(self:any, attrName:string) {
        return (
            <div>
                {attrName + ' '}
                {attrValues.map((val:any) => {
                    return <button onclick={self.setAttrVal.bind(self, attrName, val)}>{self.formatVal(val)}</button>;
                })}
                {' ' + self.printAttr(attrName)}
            </div>
        )
    }

    setPropVal(propName:string, val:any) {
        let phTest:any = this.phTest;
        phTest[propName] = val;
        this.forceRender = !this.forceRender;
    }

    setAttrVal(attrName:string, val:string) {
        let phTest:any = this.phTest;
        if (val == null) {
            phTest.removeAttribute(attrName);
        }
        else {
            phTest.setAttribute(attrName, val);
        }
        this.forceRender = !this.forceRender;
    }


    printProp(propName:string) {
        let phTest:any = this.phTest;
        let val:any = phTest ? phTest[propName] : undefined;
        return this.formatVal(val);
    }

    printAttr(attrName:string) {
        let phTest:any = this.phTest;
        let val:any = phTest ? phTest.getAttribute(attrName) : undefined;
        return this.formatVal(val);
    }

    formatVal(val:any):string {
        switch (typeof val) {
            case 'string':
                return '\'' + val + '\'';
            case 'number':
            case 'boolean':
                return val.toString();
            default:
                if (val === null) {
                    return 'null';
                }
                return typeof val;
        }
    }

}

customElements.define('ph-test-tester', PhTestTester);



