// @flow

import {Component, emit, h, prop} from 'photon-core';

import css from './ph-actions.css';

export class PhActions extends Component {

    static get props() {
        return {
            actions: prop.array({
                attribute: true,
                default: ['usd', 'wrench', 'building']
            }),
            size: prop.string({
                attribute: true,
                default: 'medium'
            })
        }
    }

    renderCallback():any {
        return (
            <div>
                <style>{css.toString()}</style>
                {this.actions.map((action, index) => {
                    return <ph-icon-badge key={index} icon={action} size={this.size}></ph-icon-badge>;
                })}
            </div>
        );
    }
}

customElements.define('ph-actions', PhActions);



